import Vue from 'vue'
import Router from 'vue-router'

const Index = () => import('../components/Index')
const List = () => import('../components/users/List')

Vue.use(Router)

const routes = [
  {path: '', redirect: '/index'},
  {path: '/index', component: Index},
  {path: '/users_list', component: List}
]

export default new Router({
  routes,
  mode: 'history'
})



